<template>
	<view class="">
		<view class="b-r-52 ta-c fw800 color-H0f649c"
		style="margin: 0 78.5rpx;  background-color: #fff;">
			<view class="" style="height: 72rpx;"></view>
			<view class=" f45rpx">One-Time Passcode</view>
			<view class=" f34rpx" style="margin: 40rpx 0 37rpx; line-height: 47rpx;">
				Please check your inbox for <br> 
				your One-Time passcode! <br>
				We have send an email to <br> 
			<view class="" style="color: #8f9090;">email@address.com</view>
				containing the 6-digit code.<br>
				Enter the code below!
			</view>
			
			<view class="b-r-15 df-jcc-aic" style="height: 90rpx; margin: 0 45.5rpx; border: 1rpx solid #ccc;">
				<view class="df-jcsa" @click="inputData">
					<view class="input-item">
						{{code[0]}}
					</view>
					<view class="input-item">
						{{code[1]}}
					</view>
					<view class="input-item">
						{{code[2]}}
					</view>
					<view class="input-item">
						{{code[3]}}
					</view>
					<view class="input-item">
						{{code[4]}}
					</view>
					<view class="input-item">
						{{code[5]}}
					</view>
				</view>
			</view> 
			
			<view @click="Continue" class="box-sha bgc-o-r fw800 f34rpx ta-c FFF"  
			style="height: 84rpx; margin: 25rpx 41.5rpx 34rpx; line-height: 84rpx; border-radius: 42rpx;">
				Continue</view>
			<view class="" style="line-height: 38rpx;">
				Didn’t receive the email? Try<br> 
				checking your junk or spam<br> 
				folders, or request a resend
			</view>
			<view class="box-sha bgc-o-r fw800 f34rpx ta-c FFF"
			style="height: 84rpx; margin: 38rpx 80rpx 0; line-height: 84rpx; border-radius: 42rpx;">
				Resend Email</view>
			<view class="" style="height: 73rpx;"></view>
		</view>
		<!-- 输入值获取 -->
		<view v-if="inputFlag" class="" style="position: relative; margin: 0 10rpx;">
			<view class="input_data df-jcsb" style="align-items: center;">
				<view class="">
					<input maxlength="6" class="" type="number" v-model="code" focus=true auto-blur=true
					@blur="inputBlur" style="height: 100%; line-height: 80rpx;"/>
				</view>
				<view  class="ta-c fw800 FFF" style="width: 100rpx; height: 80rpx; line-height: 80rpx; background-color: #00FF00;">
					OK
				</view>
			</view> 
		</view>
		<popUp ref="One1" title="Please enter a six-digit verification code"></popUp>
	</view>
	
</template>

<script>
	
	export default{
		data(){
			return{
				code:[],
				inputFlag:false
			}
		},
		methods:{
			inputData(){
				this.inputFlag = true
			}, 
			inputBlur(){
				this.inputFlag = false
			},
			Continue(){
				if(this.code.length < 6){ 
					this.$refs.One1.openPopup()
					return false
				}
			}

		}
	}
</script>

<style>
	.input-item {		
		width: 70rpx;		
		height: 60rpx;		
		font-size: 30rpx;		
		background: #eaedf0;		
		text-align: center;		 
		border-radius: 15%;		
		line-height: 61rpx;
		color: #8f9090;  	 
		margin: 0 5rpx;
		font-weight: 800;
	}
	.input_data{
		width: 730rpx; 
		height: 80rpx; 
		border-bottom: 5rpx solid #008800;
		background-color: #FFF; 
		position: absolute; 
		top: -420rpx; 
		left: 0;
	}

</style>
